@import "../../Variables.scss";
// @import "../../Components/VisualAssets/BackgroundsPlus/CircleCircuit.sass";

.user-page-container {
  background-image: url("../../Components/VisualAssets/BackgroundsPlus/UserSignupBG.png");
}

.user-form-container {
  position: absolute;
  left: 180px;
  top: 3em;
  right: 0;
  display: flex;
  flex-direction: column;
  // grid-template-columns: calc(100% - 360px) 180px;
}
.circuit-line-left {
  @include circuit-container-left;
}

/* --- Login/Signup: Images --- */

.user-image-container {
  align-self: center;
  display: grid;
  grid-template: "container";
  place-items: center;
  place-content: center;
  overflow: hidden;
  max-height: clamp(450px, 50vh, 600px);
}

.logging-in {
  position: absolute;
  display: block;
  overflow: hidden;
  height: 80vh;
  top: calc(5vh + 0.2vw);
  left: 5vw;
}
.logging-in img:first-child {
  position: fixed;
  top: 50%;
  left: calc(50% + 180px);
  transform: translate(calc(-50% - 90px), -50%);
  width: 80vw;
  max-height: 95vh;
}

.logging-in-spinner {
  position: fixed;
  top: 50%;
  left: calc(50% + 180px);
  transform: translate(calc(-50% - 90px), -50%);
  width: 25vw;
  max-width: 20%;
  z-index: 10;
}
.logging-in-spinner-text {
  text-align: center;
  font-size: calc(2em + 0.2vw);
  position: fixed;
  top: calc(53% + 5vw);
  left: calc(50% + 180px);
  transform: translate(calc(-50% - 90px), -50%);
  z-index: 11;
}

.user-image-circuit-cricle {
  grid-area: 1/1;
  width: 40vw;
}
.user-image-circuit-head {
  grid-area: 1/1;
  width: 40vw;
  z-index: 5;
}
.user-image-circuit-incorrect {
  grid-area: 1/1;
  width: 40vw;
  z-index: 5;
  filter: opacity(0.8) invert(20%) sepia(69%) saturate(6831%) hue-rotate(354deg)
    brightness(100%) contrast(102%);
}
.user-image-circuit-success {
  grid-area: 1/1;
  width: 40vw;
  z-index: 5;
  transform: scale(-1, 1);
  filter: opacity(0.9) invert(53%) sepia(68%) saturate(372%) hue-rotate(73deg)
    brightness(94%) contrast(96%);
}

/* --- Login/Signup: Form Input --- */

.user-form-input {
  row-gap: 0.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  .user-form-group {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .invalid-input {
    background-color: rgba(255, 132, 132, 0.9);
  }
  .invalid-input-message {
    color: $text-colour;
    border-radius: 3px;
    align-items: center;
  }
  input {
    width: 15vw;
    height: 2.5em;
    font-size: 1em;
    text-align: center;
  }
  label {
    text-transform: uppercase;
    font-size: 1.2em;
  }
}

/* --- Login/Signup: Already Registered Link --- */

.user-registered-link {
  font-size: 1.5em;
  padding-top: 0.5em;
}

/* --- Login/Signup: Submit --- */

.user-submit {
  padding-top: 2vh;
}

.user-submit-button {
  @include button-basic;
}
.user-submit-button:hover {
  @include button-hover;
}

/* --- Login/Signup: Messages --- */

.user-messages-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.user-message-header {
  padding-bottom: 2vh;
}

.user-messages-text-success {
  color: black;
  font-size: 1em;
}
.user-messages-text-failure {
  color: $text-colour;
  font-size: 1em;
}

.user-messages-container-success {
  padding: 2vh;
  background-color: rgba(76, 175, 80, 0.9);
  border-radius: 8px;
  padding-left: 2em;
  padding-right: 2em;
  font-weight: bold;
}

.user-messages-container-failure {
  padding: 2vh;
  background-color: rgba(252, 22, 22, 0.9);
  border-radius: 8px;
  padding-left: 2em;
  padding-right: 2em;
}
